import { _decorator, Component, Node, v2, Quat, v3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('missile')
export class missile extends Component {
    start() {
        let theangle = Math.atan2(this.node.position.y - this.node.parent.getChildByName("1").position.y,
            this.node.position.x - this.node.parent.getChildByName("1").position.x)  //弧度  
        console.log(theangle)
        let theta = theangle * 180 / Math.PI;
        console.log(theta);
        this.node.eulerAngles = v3(0, 0, theta);

    }

    update(deltaTime: number) {



    }


}

